<template>
  <div id="searchSeller">
    <div class="header">
      <van-row gutter="20">
        <van-col span="4" style="padding-right: 0px">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="vertical-align: middle;"></i>
        </van-col>
        <van-col span="16" class='search font_12' style="background:#ffffff;">
          <van-search v-model="value" placeholder="请输入关键字搜索" @search="getGoodsList1"/>
        </van-col>
        <!--<van-col span="4" class="font_12" style="padding-left: 0px" @click="$router.back(-1)">-->
          <!--取消-->
        <!--</van-col>-->
      </van-row>
    </div>
    <div class="body_box">
      <div v-show="sellerInfo.length>0">
        <van-list v-model="loading" :finished="finished" @load="getGoodsList">
          <van-card v-for="(item, index) in sellerInfo" :key="index" style="margin: 10px;border-radius: 5px" @click.native="toGoods(item)">
            <div slot="thumb">
              <img :src="item.goodslogo.split(',')[0]" v-lazy="item.goodslogo.split(',')[0]" width="100%" style="border-radius: 3px;display: block;"/>
            </div>
            <div slot="tags" class="font_weight_bold padding_bottom_5">
              {{item.goodsname}}
            </div>
            <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="max-height: 36px;height: 36px;">
              <span style="height: 36px;overflow: hidden;text-overflow: ellipsis;width: 100%;line-height: 14px;display: block;">{{item.introduce}}</span>
            </div>
            <div slot="tags" class="font_12" style="padding-top: 15px;">
              <span class="color_c10804" v-if="item.type==1">¥{{item.price}}</span>
              <span class="color_c10804" v-if="item.type==2">¥{{item.spike}}</span>
              <span class="float_right color_8b8b8b">剩余&nbsp;&nbsp;&nbsp;&nbsp;{{item.num}}</span>
            </div>
          </van-card>
        </van-list>
      </div>
      <div class="text_center" style="margin-top: 50%" v-show="sellerInfo.length==0">
        <img src="static/img/img_ssbdgsp_ss1111.png" width="30%"/>
        <div class="color_c10804" style="margin-top: 10%">搜索不到商品</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        seller:[],
        sellerInfo:[],
        show:false,
        value:'',
        loading: false,
        finished: false,
        pageNum: 1,
      }
    },
    mounted() {
      this.seller = this.$store.state.sellerInfo;
      this.getGoodsList();
    },
    methods: {
      getGoodsList1(){
        this.loading = false;
        this.finished = false;
        this.pageNum = 1;
        this.sellerInfo = [];
        this.getGoodsList();
      },
      toGoods(dataMap){
        if(dataMap.type==1){
          this.$store.commit('setGoodsInfo', dataMap);
          this.$router.push('goods');
        }else{
          this.$store.commit('setGoodsInfo', dataMap);
          this.$router.push('goodsKill');
        }
      },
      getGoodsList(){
        this.loading = true;
        let $this = this;
        let qs = require('qs');
        let data = {
          shopid:this.seller.shopid,
          userid:this.userId,
          goodsname:this.value,
          pageNum: this.pageNum
        };
        $this.$ajax.post("goods/getgoodslist", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.data.length>0){
              $this.sellerInfo = $this.sellerInfo.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else{
              $this.finished = true;
              $this.loading = false;
            }
          }
        })
        .catch(function(err) {})
      }
    }
  }
</script>

<style>

  .van-cell {
    padding: 5px 15px;
  }
  .body_box{
    margin-top: 50px;
  }
  #searchSeller .van-search input {
    color: #8b8b8b;
  }
  #searchSeller .typeList {
    text-align: center;
  }
  #searchSeller .typeList span {
    font-size: 14px;
    color: rgb(102, 102, 102)
  }
  #searchSeller .typeImg {
    display: block;
    width: 75%;
    margin: 10px auto;
  }
  #searchSeller {
    width: 100%;
  }
  #searchSeller .header {
    height: 44px;
    line-height: 44px;
    box-shadow: 0px 2px 10px lightgrey;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
    background-color: white;
  }
  #searchSeller .header img {
    vertical-align: text-top;
  }
  #searchSeller .search {
    border: 1px solid lightgray;
    border-radius: 30px;
    margin-top: 6px;
  }
  #searchSeller .header .van-col {
    text-align: center;
  }
  #searchSeller .van-search {
    padding: 0px;
  }
  #searchSeller .van-search {
    background-color: transparent!important;
  }
  #searchSeller .swiper img {
    margin: 0 auto;
    display: block;
  }
  #searchSeller .nav img {
    width: 100%;
    height: auto;
  }
  #searchSeller .navCon span {
    font-size: 13px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
  }
  #searchSeller .rightText div {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 16px;
  }
  #searchSeller .footer .van-col div {
    text-align: center;
    font-size: 14px;
  }
</style>



